    const http = require("../../utils/http.js");
    import listItem from "../../components/listItem.vue";
	export default {
        components:{
            listItem
        },
		data() {
			return {
				list_data:[]
			};
		},
        onLoad(option) {
            let type = option && option['type'] ? option['type'] : 0;
            let that = this;
            http.get(http.GET_LIST+'?type='+type, function(ret){
                that.list_data = ret.data.data;
            });
		},
	}
